<template>
	<div id="login">
		<img src="../../assets/img/loginBackground.jpg" alt="">
		<div id="content" class="x-mid">
			<div id="header">
				“佳人”瑜伽会所
			</div>
			<div id="body">
				<input type="text" placeholder="用户名" v-model="name">
				<input type="password" placeholder="密码" v-model="psd">
				<el-radio v-model="tableName" label="admin">管理员</el-radio>
				<el-radio v-model="tableName" label="member">会员</el-radio>
				<span id="loginButton" @click="login">登陆</span>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "login",
		data:function () {
			return {
				name:'',
				psd:'',
				tableName:'member',
			}
		},
		methods:{
			login:function () {
				let _this = this;
				this.axios.post('/getMsg/login',{username:this.name,password:this.psd,tableName:this.tableName}).then(function (response) {
					let res=response.data;
					if(res.code==200){
						_this.$message.success(res.msg);
						localStorage.userName=res.userName;
						localStorage.userId=res.id;
						localStorage.userType=(res.type==1?'admin':'member');
						localStorage.login='true';
						setTimeout(function () {
							location.reload();
						},2000)
					}
					else
						_this.$message.error(res.msg);
				}).catch(function () {
					_this.$message.error('用户名或密码错误，请重试');
				})
			}
		},
		mounted:function () {
			if(localStorage.login){
				this.$router.replace({path:'/'});
			}
			let oLogin=document.querySelector('#login');
			oLogin.height=window.innerHeight;
		},
		computed:{
		},
		components:{
		}
	}
</script>

<style scoped>
	@import "../../assets/css/reset.css";
	#login{
		overflow: hidden;
	}
	img{
		width: 100%;
		height: 101%;
	}
	#content{
		top: 20%;
	}
	#header{
		padding: 15px 20px;
		width: 300px;
		height: 20px;
		background-color: #19AA8D;
		color: white;
		text-align: center;
		border-radius: 5px 5px 0 0;
		font-size: 21px;
	}
	#body{
		width: 300px;
		background-color: white;
		padding: 15px 20px;
		border-radius: 0 0 5px 5px;
	}
	.input-div{
		margin-bottom: 5px;
	}
	input{
		width: 280px;
		height: 20px;
		border-radius: 5px;
		border: 1px solid #eaeaea;
		padding: 10px;
		margin: 0px auto 15px;
	}
	#loginButton{
		background-color: #F67A6E;
		color: white;
		width: 290px;
		height: 45px;
		border-radius: 5px;
		display: block;
		text-align: center;
		line-height: 45px;
		font-size: 21px;
		margin:20px auto 10px;
		cursor: pointer;
	}
</style>



